.link {
  text-decoration: none;
  outline: none;
  -webkit-tap-highlight-color: transparent;
  color: var(--ui-color-primary);
  font-size: var(--ui-font-size-md);
  line-height: var(--ui-line-height-md);

  &:hover {
    color: var(--ui-color-primary-lightened);
  }

  &:focus {
    color: var(--ui-color-primary-darkened);
  }

  &:focus-visible {
    border-radius: var(--ui-element-border-radius-small);
    outline: var(--ui-border-width-medium) solid var(--ui-color-primary);
    outline-offset: 1px;
  }
}